@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background-color: var(--background);
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--background);
  font-family: "Roboto", sans-serif;
}

button {
  font-family: "Roboto", sans-serif;
}

:root {
  --background: rgb(15, 15, 15);
}

#homeTitleContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#homeContainer {
  width: 50%;
  background-color: white;
  padding: 2rem;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

#playButton {
  width: 200px;
  height: 75px;
  border: 2px solid rgb(15, 15, 15);
  border: 2px solid var(--background);
  background-color: white;
  border-radius: 999px;
  font-size: 1.25rem;
  color: rgb(15, 15, 15);
  color: var(--background);
  transition: background-color 0.4s ease, color 0.4s ease;
}

#playButton:hover {
  background-color: rgb(87, 248, 72);
}

#robotSVG {
  width: 100px;
  height: auto;
}

:root {
  --accent: rgb(87, 248, 72);
  --background: rgb(15, 15, 15);
}

#chartContainer {
  display: flex;
}

#chart {
  background: rgb(15, 15, 15);
  background: var(--background);
  margin-bottom: 2rem;
}

#yAxis > .domain {
  stroke-width: 0;
}

#yAxis > .tick line {
  stroke-opacity: 0.5;
  stroke-dasharray: 2;
  stroke: rgb(87, 248, 72);
  stroke: var(--accent);
}

#yAxis > .tick text {
  transform: translate(-980px, -8px);
  fill: rgb(87, 248, 72);
  fill: var(--accent);
  font-size: 12px;
}

#xAxis > .domain,
#xAxis > .tick line {
  stroke: rgb(87, 248, 72);
  stroke: var(--accent);
}

#xAxis > .tick text {
  fill: rgb(87, 248, 72);
  fill: var(--accent);
  font-size: 12px;
}

#path {
  stroke: rgb(87, 248, 72);
  stroke: var(--accent);
  fill: none;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
}

#chartContainer {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#chartTitle {
  color: white;
}

.col {
}

#col1 {
  flex: 2 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

#col2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 8 1;
  height: 100%;
}

#col3 {
  flex: 2 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#controlsContainer {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

#sliderLabel {
  color: white;
  transform: translateY(-30px);
}

#slideContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button {
  width: 200px;
  height: 75px;
  color: rgb(15, 15, 15);
  color: var(--background);
  font-size: 1.25rem;
  border-radius: 999px;
  border: none;
}

.button:focus {
  outline: 0;
  box-shadow: 0;
}

#buyButton {
  background-color: rgb(87, 248, 72);
  background-color: var(--accent);
}

#buyButton:focus {
  -webkit-animation: buy 400ms ease-out;
          animation: buy 400ms ease-out;
}

@-webkit-keyframes buy {
  0% {
    background-color: rgb(87, 248, 72);
    background-color: var(--accent);
  }
  50% {
    background-color: rgb(171, 255, 163);
  }
  100% {
    background-color: rgb(87, 248, 72);
    background-color: var(--accent);
  }
}

@keyframes buy {
  0% {
    background-color: rgb(87, 248, 72);
    background-color: var(--accent);
  }
  50% {
    background-color: rgb(171, 255, 163);
  }
  100% {
    background-color: rgb(87, 248, 72);
    background-color: var(--accent);
  }
}

#sellButton {
  background-color: red;
}

#sellButton:focus {
  -webkit-animation: sell 400ms ease-out;
          animation: sell 400ms ease-out;
}

@-webkit-keyframes sell {
  0% {
    background-color: red;
  }
  50% {
    background-color: rgb(255, 98, 98);
  }
  100% {
    background-color: red;
  }
}

@keyframes sell {
  0% {
    background-color: red;
  }
  50% {
    background-color: rgb(255, 98, 98);
  }
  100% {
    background-color: red;
  }
}

.dot {
  fill: none;
}

.dot.playerBuy {
  stroke: rgb(87, 248, 72);
  stroke: var(--accent);
  fill: rgb(87, 248, 72);
  fill: var(--accent);
}

.dot.playerSell {
  stroke: red;
  fill: red;
}

.dot.aiBuy {
  stroke: rgb(87, 248, 72);
  stroke: var(--accent);
}

.dot.aiSell {
  stroke: red;
}

/* Slider Bar Styling */
input[type="range"] {
  -webkit-appearance: none;
  margin: 18px 0;
  width: 300px;
  background-color: rgb(15, 15, 15);
  background-color: var(--background);
}
input[type="range"]:focus {
  outline: none;
}
input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  background: rgb(87, 248, 72);
  background: var(--accent);
  border-radius: 1.3px;
  border: 0.2px solid #010101;
}
input[type="range"]::-webkit-slider-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 16px;
  border-radius: 3px;
  background: rgb(87, 248, 72);
  background: var(--accent);
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -14px;
}

input[type="range"]::-moz-range-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  background: rgb(87, 248, 72);
  background: var(--accent);
  border-radius: 1.3px;
  border: 0.2px solid #010101;
}
input[type="range"]::-moz-range-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 16px;
  border-radius: 3px;
  background: rgb(87, 248, 72);
  background: var(--accent);
  cursor: pointer;
}
input[type="range"]::-ms-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  border-width: 16px 0;
  color: transparent;
}
input[type="range"]::-ms-fill-lower {
  background: rgb(87, 248, 72);
  background: var(--accent);
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}
input[type="range"]::-ms-fill-upper {
  background: rgb(87, 248, 72);
  background: var(--accent);
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}
input[type="range"]::-ms-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 16px;
  border-radius: 3px;
  background: rgb(87, 248, 72);
  background: var(--accent);
  cursor: pointer;
}
input[type="range"]:focus::-ms-fill-lower {
  background: rgb(87, 248, 72);
  background: var(--accent);
}
input[type="range"]:focus::-ms-fill-upper {
  background: rgb(87, 248, 72);
  background: var(--accent);
}

#resultsHTML {
  position: absolute;
  background-color: rgb(15, 15, 15);
  background-color: var(--background);
  padding: 2rem;
  width: 400px;
  height: 300px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  border: 2px solid rgb(87, 248, 72);
  border: 2px solid var(--accent);
  color: white;
  border-radius: 10px;
  font-size: 1.5rem;
}

div#resultsTitle {
  font-size: 2rem;
  text-align: center;
}

#resultsHTML > hr {
  width: 250px;
  border: 3px solid rgb(87, 248, 72);
  border: 3px solid var(--accent);
  border-radius: 2px;
}

#resultsButton {
  width: 200px;
  height: 75px;
  color: rgb(15, 15, 15);
  color: var(--background);
  font-size: 1.25rem;
  border-radius: 999px;
  border: none;
  background: rgb(87, 248, 72);
  background: var(--accent);
  margin-top: 1rem;
}

#resultsButton:focus {
  outline: 0;
}

#legendContainer {
  color: white;
}

.legendRow {
  display: flex;
  padding: 1rem;
  align-items: center;
}

.svgCircle {
  margin-right: 1rem;
}

#playerBuyLegend {
  fill: rgb(87, 248, 72);
}

#playerSellLegend {
  fill: red;
}

#aiBuyLegend {
  stroke: rgb(87, 248, 72);
  fill: none;
}
#aiSellLegend {
  stroke: red;
  fill: none;
}

#scoreboardContainer {
  color: white;
}

.stats {
  font-size: 1rem;
}

.statsText {
  height: 2rem;
}

.statsGroup {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
}

.statsText.time {
  color: white;
}

.trendUp {
  fill: rgb(87, 248, 72);
}

.trendDown {
  fill: red;
}

.buy {
  color: rgb(87, 248, 72);
}

.sell {
  color: red;
}

